<template>
  <v-container>
    <v-row class="text-center" dense>
      <v-col cols="6">
        <v-card >
          <v-card-title> <v-icon>mdi-weather-pouring</v-icon>&nbsp;Rain Rate</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-text><h1>{{RainRate}} mm</h1>
          <br>
          Last update: {{updateTime(timeStamp)}}
          </v-card-text>
        </v-card>
      </v-col>
      <v-col>
        <v-card >
          <v-card-title> Daily Rain Rate</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-text><h1>{{RainRateDay}} mm</h1>
          <br>
          Last rain: {{updateTime(lastRain)}}
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row class="text-left" dense>
      <v-col cols="12">
        <v-card >
          <v-card-title> <v-icon>mdi-speedometer</v-icon>&nbsp;Device Details</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-text>
            Signal: {{signal}} <br>
            Battery Voltage: {{battery}} Volt <br>
            Last update: {{updateTime(timeStamp)}} <br>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row class="text-left" dense>
      <v-col cols="12">
        <v-card >
          <v-card-title> <v-icon>mdi-switch</v-icon>&nbsp;Device Switch</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-text>
            <span v-for="(item,index) in Switch" :key="item">
              Switch <b>{{index+1}}</b>: <b>{{item=='0'?'Closed':'Open'}}</b> <br>
            </span>
            <br>
            Last Update: {{updateTime(TimeIO)}}
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row class="text-left" dense>
      <v-col cols="12">
        <v-card >
          <v-card-title> <v-icon>mdi-sd</v-icon>&nbsp;Download Data</v-card-title>
          <v-divider class="mx-2 mb-2"></v-divider>
          <v-card-text>
            <v-data-table
              :headers="headers"
              :items="Files"
              :items-per-page="10"
              class="elevation-1"
              mobile-breakpoint="300"
            >
              <template v-slot:[`item.timestamp`]="{ item }">
                {{updateTime(item.timestamp)}}
              </template>
              <template v-slot:[`item.id`]="{ item }">
                <v-btn icon @click="clicked(item)" color="primary">
                  <v-icon>
                    mdi-download
                  </v-icon>
                </v-btn>
              </template>
            </v-data-table>

            <v-dialog
              v-model="dialog"
              max-width="600"
            >
              <v-card>
                <v-toolbar
                  color="primary text-h6"
                  dark
                >{{ deviceName }}
                <v-spacer></v-spacer>
                
                </v-toolbar>
                <v-card-actions>
                  <download-csv
                    class   = "btn btn-default"
                    :data   = "downloadData"
                    :name    = "csvTitle"
                    separator-excel>
                    <v-btn class="float-start mb-2" color="primary" text><v-icon left>mdi-download</v-icon>Download</v-btn>
                  </download-csv>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="primary"
                    text
                    @click="dialog = false"
                  >
                    Close
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-card-text>
        </v-card>

        <v-row class="text-center">
          <v-col>
            <v-btn class="error darken-1 mt-2" @click="reset($props.device_id)"> <v-icon left>mdi-restart</v-icon>Restart Device</v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { db } from '../firebase/db.js'
import csvtojson from 'csvtojson'
import moment from 'moment'

  export default {
    props: ['device_id'],
    name: 'DashboardData',

    data: () => ({
      RainRate:null,
      RainRateDay:null,
      lastRain:null,
      timeStamp:null,
      offset:null,
      signal:null,
      battery:null,
      TimeIO:null,
      Switch:[],
      headers: [
        {
          text: 'Date',
          align: 'start',
          sortable: false,
          value: 'timestamp',
        },
        { text: 'Download', value: 'id', align:'end' },
      ],
      Files:[],
      csvTitle:null,
      deviceName:null,
      downloadData:[],
      dialog:false,
    }),
    async mounted () {
      const date=new Date();
      this.offset=date.getTimezoneOffset()/(-60)
      await db.collection('Ombrometer').doc(this.$props.device_id).onSnapshot((doc)=> {
        this.RainRate=parseFloat(doc.data().RainRate).toFixed(2)
        this.RainRateDay=parseFloat(doc.data().RainRateDay).toFixed(2)
        this.lastRain=parseInt(doc.data().TimeRain)
        this.timeStamp=parseInt(doc.data().Timestamp)
        this.signal=parseInt(doc.data().Signal)
        this.battery=parseFloat(doc.data().Battery).toFixed(3)
        this.Switch=doc.data().IO
        this.TimeIO=doc.data().TimeIO
        this.deviceName=doc.data().station
      })
      await db.collection('Ombrometer').doc(this.$props.device_id).collection('SDFiles').orderBy('time','desc').onSnapshot((docs)=> {
        this.Files=[]
        docs.forEach((doc)=> {
          this.Files.push({
            timestamp: doc.data().time/1000,
            data: doc.data().data,
            id:doc.id
          })
        })
      })
    },
    methods: {
      updateTime (time) {
        return moment.unix(time).utcOffset(this.offset).format("HH:mm, DD MMM YYYY")
      },
      clicked(item) {
        this.dialog=true
        this.csvTitle=`LOG_${this.deviceName}-${this.updateTime(item.timestamp)}.csv`
        this.downloadData=[]
        csvtojson()
          .fromString(item.data)
          .then((csvRow)=>{ 
              this.downloadData=csvRow
          })
        console.log(this.downloadData)
      },
      async reset(id) {
        await db.collection('Ombrometer').doc(id).update({
          resetDevice:1
        })
      }
    }
  }
</script>
